@import url('https://fonts.googleapis.com/css2?family=Epilogue:wght@300&500&display=swap');

body {
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #0d1019;
    font-family: 'Epilogue', sans-serif;

}

.test {
    color: white;
}

.products-headers {
    display: inline-flex;
    justify-content: space-evenly;
    width: 99%;
    height: 10%;
}

.product-group-header {
    width: 25%;
    border-bottom: .25vh solid #0984e3;
}

.product-group-header h3 {
    font-size: 3vh;
    color: white;
}

.products-div {
    display: inline-flex;
    width: 100%;
    height: 80vh;

}

.products-page-div {
    width: 100%;
    height: 100%;
    text-align: center;
}

.products-div-left, .products-div-right {
    display: inline-flex;
    width: 50%;
    height: 100%;

}

.products-div-left-left, .products-div-left-right, .products-div-right-left, .products-div-right-right {
    display: inline-block;
    width: 50%;
    height: 100%;
    opacity: .5;
}

.products-div-left-left:hover {
    animation: opacity_in .5s;
    opacity: 1;
}

.products-div-left-right:hover {
    opacity: 1;
    animation: opacity_in .5s;
}

.products-div-right-left:hover {
    opacity: 1;
    animation: opacity_in .5s;
}

.products-div-right-right:hover {
    opacity: 1;
    animation: opacity_in .5s;
}


@keyframes opacity_in {
	0% {
        opacity: .25;
	}

    33% {
        opacity: .5;
	}

	66% {
        opacity: .75;
	}

	100% {
        opacity: 1;
	}
}